@use "global" as *;

.card__block {
    display: block;
    height: auto;
    background-color: $white;
    box-shadow: 0 rem(4) rem(4) rgba($color:#000, $alpha:0.25);
    width: rem(280);
    @include mq(md) {
        width: rem(333);
    }
}
.card__block--full {
    width: 100%;
    @include mq(md) {
    width: 100%;
    }
}
.card__img img {
    aspect-ratio: 280/188;
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include mq(md) {
        aspect-ratio: 333/223;
    }
}
.card__category {
    display: inline-block;
    font-size: rem(12);
    font-weight: $regular;
    line-height: 1.83;
    color: $accent;
    border: 1px solid $accent;
    padding: rem(3) rem(11);
    @include mq(md) {
        padding: rem(5) 1rem;
    }
}
.card__title {
    font-weight: $medium;
    line-height: 1.5;
    margin-top: rem(4);
    font-size: 1rem;
}
.card__title--bg {
    @include mq(md) {
    font-size: rem(24);
    }
}
.card__title-body {
    margin-top: 1rem;
    @include mq(md) {
        margin-top: rem(24);
    }
}
.card__info {
    margin: 0 1rem;
}
.card__price-body {
    padding: rem(6) 0 rem(15);
    margin-top: rem(6);
    border-top: 1px solid #408F95;
    @include mq(md) {
        padding: rem(10) 0 rem(40);
        margin-top: rem(8);
    }
}
.card__text {
    font-size: rem(14);
    line-height: 2;
    @include mq(md) {
        font-size: 1rem;
    }
}
.card__text.card__text--center {
    text-align: center;
}
.card__price-block {
    display: flex;
    justify-content: center;
    gap: 1rem;
    align-items: center;
    @include mq(md) {
        line-height: 1;
        align-items: end;
        margin-top: rem(6);
        gap: rem(24);
    }
}
.card__price {
    font-size: 2rem;
    font-weight: bold;
    font-family: $english-font2;
    color: $accent;
    @include mq(md) {
        font-size: rem(40);
    }
}
.card__price-decoration {
    font-family: $english-font2;
    font-size: rem(20);
    font-weight: bold;
    text-decoration: line-through 3px solid $red;
    @include mq(md) {
        font-size: rem(24);
    }
}
.card a:hover {
    opacity: .6;
}

.card__img--big img {
    @include mq(md) {
        aspect-ratio: 520/347;
    }
}
.card__title--big {
    @include mq(md) {
        font-size: rem(24);
    }
}
.card__title-body--big {
    margin: 0;
}
.card__price-block--big {
    @include mq(md) {
        gap: rem(44);
    }
}
.card__img--side img {
    aspect-ratio: 294/188;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.card__title-body--side {
    margin-top: rem(15);
    text-align: center;
}
.card__text.card__text--side {
    font-size: rem(14);
    line-height: initial;
}
.card__price.card__price--side {
    font-size: rem(32);
}
.card__price-decoration--side {
    font-size: rem(20);
}
.card__price-body--side {
    padding: rem(10) 0 rem(26);
}
.card__price-text {
    padding: 1rem 0 0;
    margin: 0 1rem ;
    @include mq(md) {
        padding: rem(24) 0 rem(40);
        margin: 0 rem(24);
    }
}
.card__price-text .card__info {
    padding: 0;
    margin: 0;
}
.card-text__explanation {
    margin-top: 0;
}
.card__price-body {
    padding: rem(6) 0 rem(15);
    margin-top: rem(6);
    border-top: 1px solid #408F95;
    @include mq(md) {
        padding: rem(18) 0 rem(25);
        margin-top: 0;
    }
}