.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 0.875rem;
  width: 12.625rem;
  height: 3.375rem;
  color: #fff;
  font-family: "Gotu", sans-serif;
  background-color: #408F95;
  border: 1px solid #408F95;
  position: relative;
  z-index: 1;
}

.btn::before {
  content: "";
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.3s transform;
  z-index: -1;
}

.btn::after {
  content: "";
  border: 1px solid #408F95;
  position: absolute;
  width: 12.625rem;
  height: 3.375rem;
  top: 0.25rem;
  left: 0.25rem;
  transition: 0.3s border;
  z-index: -2;
}

.btn span {
  display: inline-block;
  vertical-align: middle;
  margin-left: 0.5rem;
  width: 2.5rem;
  height: 0.0625rem;
  background-color: #fff;
  border-radius: 0.0625rem;
  position: relative;
  transition: 0.3s background-color;
}

.btn span::after {
  content: "";
  display: block;
  width: 0.625rem;
  height: 0.0625rem;
  background-color: #fff;
  position: absolute;
  top: -0.2rem;
  right: -0.09375rem;
  border-radius: 0.0625rem;
  transform: rotate(45deg);
  transition: 0.3s background-color;
}

.btn:hover {
  color: #408F95;
}

.btn:hover span,
.btn:hover span::after {
  background-color: #408F95;
}

.btn:hover::before {
  transform: scale(1, 1);
}

.btn.btn--accent {
  color: #408F95;
  background-color: #fff;
}

.btn.btn--accent span {
  background-color: #408F95;
}

.btn.btn--accent span::after {
  background-color: #408F95;
}

.btn.btn--accent::after {
  border: 1px solid #fff;
}

.btn.btn--accent:hover {
  color: #fff;
  border: 1px solid #fff;
